.topbar{
    width: 100%;
    height:50px;
    background-color: white;
    position:sticky;
    top:0;
    z-index: 1;
}
.topbarwrapper{
    height:100%;
    padding:0px 20px;
    display:flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    font-weight: bold;
    font-size: 30px;
    color: darkblue;
    cursor:pointer;
}

.topbarIcon{
    position: relative;
   cursor: pointer;
   margin-right: 10px;
}

.topRight{
  display: flex;
  align-items: center;
  color:#555;
}
.icconBadge{
    width:15px;
    height:15px;
    position:absolute;
    top:-5px;
    right:0px;
    background-color: red;
    color:white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  font-size: 10px;
}

.Avatar{
    width:40px;
    height:40px;
    border-radius: 50%;
   cursor:pointer;
}

